<template>
  <div>
    <header>
      <van-nav-bar title="" left-text="" left-arrow  @click-left="ToMyOne()">
        <van-icon />
      </van-nav-bar>
    </header>
    <section>
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        :disabled-list="disabledList"
        disabled-text=""
        @add="onAdd"
        @edit="onEdit"
      />
    </section>

  </div>
</template>

<script>
export default {
  name:"MyAddress",
  data() {
    return {
      chosenAddressId: '1',
      list:[
        {
          id: '1',
          name: '',
          tel: '13223225689',
          address: ' 138  7  501 '
        },
        {
          id: '2',
          name: '',
          tel: '18956478965',
          address: ' 50 '
        }
      ],
       disabledList:[
         {
          id: '3',
          name: '',
          tel: '13365489654',
          address: ' 15 '
        }
       ]
    }
  },
  methods: {
    ToMyOne(){
      history.go(-1)
    },
    onAdd() {
     this.$router.push("/addmyaddress")
    },
    onEdit(item, index) {
      Toast(':' + index);
    }
  },
}
</script>

<style scoped="">

</style>



