import axios from '../utils/request';

/*
*
* @params {*} params
* per   
* page  
 */

export const getProducts = (params) => {
  return axios.get('/api/v1/products',{ params });
}


/*
*   
*   @params {*} id  id
*/
export const getProductDetail = (id) => {
  return axios.get(`/api/v1/products/${id}`);
}



